export async function POST(request: Request) {
  try {
    const { name, email, message } = await request.json();

    // Validate input
    if (!name || !email || !message) {
      return Response.json(
        { error: 'Missing required fields' },
        { status: 400 }
      );
    }

    // Send using Resend service (free tier available)
    // For now, we'll use a simple approach with FormSubmit.co
    const response = await fetch('https://formsubmit.co/ajax/snehalganpatiselukar@gmail.com', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        name: name,
        email: email,
        message: message,
        _subject: `New Portfolio Contact from ${name}`,
        _captcha: 'false',
      }),
    });

    if (!response.ok) {
      throw new Error('Failed to send email');
    }

    return Response.json(
      { success: true, message: 'Email sent successfully' },
      { status: 200 }
    );
  } catch (error) {
    console.error('Email error:', error);
    return Response.json(
      { error: 'Failed to send email' },
      { status: 500 }
    );
  }
}
